import React from 'react';
import { useSelector } from 'react-redux';
import { Header2 } from 'components/typography';
import NumberItem from './item';
import Wrap from 'components/wrap';

import useStyles from './use-styles';

export default function Numbers(props) {
	const styles = useStyles();
	const { payload } = useSelector((state) => state.HomeInfo);
	const items = [
		{
			number: payload && payload.years,
			description: payload && payload.yearsText,
		},
		{
			number: payload && payload.projectCount,
			description: 'Projects we developed',
			link: 'Learn more',
		},
		{
			number: 28,
			description: '	People currently we have in the team',
			link: 'Learn more',
		},
	];

	return (
		<section className={styles.numbers}>
			<Wrap>
				<Header2 className={styles.headerOffest} title='TGA Numbers' />
				<div className={styles.numbersList}>
					{items.map((item) => (
						<NumberItem
							number={item.number}
							description={item.description}
							link={'Learn more'}
						/>
					))}
				</div>
			</Wrap>
		</section>
	);
}
